export default class  Storage {
    constructor(key) {
        this.key = key;
        this.storageArea = null; 
    }

/**
     * Establece el 谩rea de almacenamiento y devuelve la instancia para encadenar.
     * @param {string} area - 'local' o 'sync'.
     */
    async setStorageArea(area) {
        this.storageArea = (area === 'sync') ? chrome.storage.sync : chrome.storage.local;
        console.log(`rea de almacenamiento establecida en: ${this.storageArea === chrome.storage.sync ? 'sync' : 'local'}`);
        return this; // Devolvemos la instancia
    }

    async getFolders() {
        // Usamos la propiedad din谩mica 'storageArea'
        const data = await this.storageArea.get(this.key);
        return data[this.key] || {};
    }

    async saveFolders(folders) {
        // Usamos la propiedad din谩mica 'storageArea'
        return this.storageArea.set({ [this.key]: folders });
    }

    /**
     * Obtiene la configuraci贸n de sincronizaci贸n del usuario.
     * @returns {Promise<boolean>} - true si la sincronizaci贸n est谩 habilitada, false en caso contrario.
     */
    async getSyncEnabled() {
        const data = await chrome.storage.sync.get('syncEnabled');
        return data.syncEnabled || false;
    }

    /**
     * Guarda la configuraci贸n de sincronizaci贸n del usuario.
     * @param {boolean} enabled - El estado de la sincronizaci贸n.
     */
    async setSyncEnabled(enabled) {
        return chrome.storage.sync.set({ syncEnabled: enabled });
    }
}